<!--
 * @Description: 分销员列表
 * @Author: your name
 * @LastEditors: Please set LastEditors
 -->
<template>
  <div>
    <!-- 筛选条件 -->
    <el-card class="filter-container"
             shadow="never">
      <div>
        <i class="el-icon-search"></i>
        <span>筛选搜索</span>
        <el-button style="float: right"
                   @click="searchList"
                   type="primary"
                   size="small">
          查询
        </el-button>
        <el-button style="float: right;margin-right: 15px"
                   @click="reset"
                   size="small">
          重置
        </el-button>
      </div>
      <div style="margin-top: 40px">
        <el-form :inline="true"
                 :model="list"
                 size="small"
                 label-width="140px">
          <el-form-item label="分销员昵称：">
            <el-input v-model="list.channelUsernickName"
                      clearable
                      placeholder="分销员昵称"></el-input>
          </el-form-item>
          <el-form-item label="分销员手机号：">
            <el-input v-model="list.channelUserPhone"
                      clearable
                      placeholder="分销员手机号"></el-input>
          </el-form-item>
          <el-form-item label="邀请方昵称：">
            <el-input v-model="list.inviternickName"
                      clearable
                      placeholder="邀请方昵称"></el-input>
          </el-form-item>
          <el-form-item label="邀请方手机号：">
            <el-input v-model="list.inviterPhone"
                      clearable
                      placeholder="邀请方手机号"></el-input>
          </el-form-item>
          <el-form-item label="是否有邀请方：">
            <el-select v-model="list.invitationState"
                       clearable
                       placeholder="默认选择全部">
              <el-option v-for="item in invitationStateList"
                         :key="item.value"
                         :label="item.label"
                         :value="item.value">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="加入时间：">
            <el-date-picker v-model="list.registerTime"
                            type="datetimerange"
                            value-format="yyyy-MM-dd HH-mm-ss"
                            :picker-options="pickerOptions"
                            range-separator="至"
                            start-placeholder="开始日期"
                            end-placeholder="结束日期">
            </el-date-picker>
          </el-form-item>
          <el-form-item label="按分组查看：">
            <el-select v-model="list.groupId"
                       clearable
                       placeholder="默认选择全部">
              <el-option v-for="item in groupList"
                         :key="item.id"
                         :label="item.name"
                         :value="item.id">
              </el-option>
            </el-select>
          </el-form-item>
        </el-form>
      </div>
    </el-card>
    <!-- 数据列表 -->
    <el-card style="margin-top:10px"
             shadow="never">
      <div class="flex-row-between">
        <div>
          <i class="el-icon-tickets"></i>
          <span>数据列表</span>
          <span style="margin-left:20px;color: #f59a23;font-weight: 500;font-size:20px">分销员总数：{{total}}</span>
        </div>
        <div>
          <el-button type="primary"
                     @click="exportExcel()"
                     size="mini">
            导出Excel
          </el-button>
        </div>
      </div>

    </el-card>
    <el-card style="margin-top:10px"
             shadow="never">
      <el-table :data="tableList"
                style="width: 100%"
                ref="multipleTable"
                v-loading="loading"
                @selection-change="handleSelectionChange"
                border>
        <el-table-column type="selection"
                         width="55">
        </el-table-column>
        <el-table-column label="分销员昵称"
                         width="140"
                         align="center">
          <template slot-scope="scope">{{scope.row.channelUsernickName}}</template>
        </el-table-column>
        <el-table-column label="分销员手机号"
                         width="120"
                         prop="channelUserPhone"
                         align="center">
        </el-table-column>
        <el-table-column label="标识码"
                         width="160"
                         prop="inviteCode"
                         align="center">
        </el-table-column>
        <el-table-column label="所属分组"
                         width="200"
                         prop="channelGroupName"
                         align="center">
        </el-table-column>
        <el-table-column label="邀请方昵称"
                         width="160"
                         align="center">
          <template slot-scope="scope">
            {{ scope.row.inviternickName == '' ? '--' : scope.row.inviternickName }}
          </template>
        </el-table-column>
        <el-table-column label="邀请方手机号"
                         width="130"
                         align="center">
          <template slot-scope="scope">
            {{ scope.row.inviterPhone == '' ? '--' : scope.row.inviterPhone }}
          </template>
        </el-table-column>
        <el-table-column width="130"
                         prop="orderTotal"
                         align="center">
          <template slot="header"
                    slot-scope="scope">
            <el-tooltip class="item"
                        effect="dark"
                        content="累计成交笔数是指该分销员转发成功下单和有绑定关系的客户成功下单；"
                        placement="top">
              <el-button style="color:#909399;font-weight:600"
                         type="text">累计成交笔数<i class="el-icon-question"></i></el-button>
            </el-tooltip>
          </template>
        </el-table-column>
        <el-table-column width="165"
                         prop="orderAmount"
                         align="center">
          <template slot="header"
                    slot-scope="scope">
            <el-tooltip class="item"
                        effect="dark"
                        content="累计分销商品交易额是指该分销员转发成功下单和邀请成功的客户关联的订单支付成功的分销商品交易额；"
                        placement="top">
              <el-button style="color:#909399;font-weight:600"
                         type="text">累计分销商品交易额<i class="el-icon-question"></i></el-button>
            </el-tooltip>
          </template>
        </el-table-column>
        <el-table-column label="累计分到佣金（不剔除退款）"
                         width="240"
                         prop="commissionAmount"
                         align="center">
        </el-table-column>
        <el-table-column label="累计分到邀请奖励"
                         width="180"
                         prop="inviteAmount"
                         align="center">
        </el-table-column>
        <el-table-column label="加入时间"
                         width="160"
                         prop="createTime"
                         align="center">
        </el-table-column>
        <el-table-column label="状态"
                         width="120"
                         align="center">
          <template slot-scope="scope">
            <span v-if="scope.row.state === 0">正常</span>
            <span v-if="scope.row.state === 1">锁定</span>
          </template>
        </el-table-column>
        <el-table-column label="操作"
                         width="300"
                         align="center">
          <template slot-scope="scope">
            <el-button type="text"
                       v-if="scope.row.state === 1"
                       class="button_mian"
                       @click="auditTrue(0,scope.row)">解冻</el-button>
            <el-button type="text"
                       v-if="scope.row.state === 0"
                       class="button_mian"
                       @click="auditTrue(1,scope.row)">冻结</el-button>
            <el-button type="text"
                       class="button_mian"
                       @click="showDetails(scope.row)">查看绑定客户</el-button>
          </template>
        </el-table-column>
      </el-table>
      <div class="flex-row-between"
           style="padding-top:20px;">
        <div>
          <el-button type="primary"
                     @click="changeGroup">批量修改分组</el-button>
        </div>
        <!-- 页码 -->
        <el-pagination background
                       style="margin-top: 10px;margin-left:20px"
                       @size-change="handleSizeChange"
                       @current-change="pageChange"
                       layout="total,sizes,prev,pager,next,jumper"
                       :page-size="page_size"
                       :page-sizes="[10, 30, 50]"
                       :current-page="pageNum"
                       :total="total">
        </el-pagination>
      </div>
    </el-card>
    <el-dialog title="分销员解冻"
               :visible.sync="dialogFormVisible">
      <span style="margin-left:10%;font-weight:600">您确定要解冻该分销员？</span>
      <div slot="footer"
           class="dialog-footer">
        <el-button @click="dialogFormVisible = false">取 消</el-button>
        <el-button type="primary"
                   @click="audit(0)">确 定</el-button>
      </div>
    </el-dialog>
    <el-dialog title="分销员冻结"
               :visible.sync="dialogFormVisibleOne">
      <span style="margin-left:10%;font-weight:600">冻结账号后分销员分享不再得钱，商城会做提示该分销员已冻结，但是可以能提现，冻结后分销员不再分佣。</span>
      <div slot="footer"
           class="dialog-footer">
        <el-button @click="dialogFormVisibleOne = false">取 消</el-button>
        <el-button type="primary"
                   @click="audit(1)">确 定</el-button>
      </div>
    </el-dialog>
    <el-dialog title="设置分销员分组"
               :visible.sync="dialogFormVisibleGroup"
               width="30%">
      <el-form size="small"
               label-width="140px">
        <el-form-item label="按分组查看：">
          <el-select v-model="group.groupId"
                     placeholder="默认分组">
            <el-option v-for="item in groupList"
                       :key="item.id"
                       :label="item.name"
                       :value="item.id">
            </el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <div slot="footer"
           class="dialog-footer">
        <el-button @click="dialogFormVisibleGroup = false">取 消</el-button>
        <el-button type="primary"
                   @click="setDistriGroup">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>
<script>
import {objectUrl} from '@/utils/commom.js'
import {
  channelUserList,
  updateChannelUserStatus,
  initChannelGroupList,
  groupCapacity,
  findChannelGroupList,
  changeChannelUserGroup,   //批量修改分组
} from '@/api/distributionManagement/distributor'
// import testJson from './test'
export default {
  props: ["status", "user", "shop"],
  data() {
    return {
      //列表筛选条件
      list: {
        channelUsernickName: '',
        channelUserPhone: '',
        invitationState: '',
        inviternickName: '',
        inviterPhone: '',
        groupId: '',
        registerTime: '',
      },
      // 弹窗列表分组
      group: {
        groupId: '',
      },


      dialogFormVisible: false,
      dialogFormVisibleOne: false,
      dialogFormVisibleGroup: false,
      id: '',
      tableList: [],
      multipleSelection: [],
      groupList: [],
      pageNum: 1,
      page_size: 10,
      total: 0,
      loading: false,
      pickerOptions: {
        disabledDate(time) {
          return time.getTime() > Date.now()
        },
        shortcuts: [
          {
            text: '今天',
            onClick(picker) {
              const end = new Date();
              end.setTime(end.getTime());
              end.setHours(end.getHours());
              end.setMinutes(end.getMinutes());
              end.setSeconds(end.getSeconds());
              const start = new Date();
              start.setTime(start.getTime());
              start.setHours(0); start.setMinutes(0); start.setSeconds(0);
              picker.$emit('pick', [start, end]);
            }
          }, {
            text: '昨天',
            onClick(picker) {
              const end = new Date();
              end.setTime(end.getTime() - 3600 * 1000 * 24);
              end.setHours(23);
              end.setMinutes(59);
              end.setSeconds(59);
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24);
              start.setHours(0); start.setMinutes(0); start.setSeconds(0);
              picker.$emit('pick', [start, end]);
            }
          },
          {
            text: '最近一周',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit('pick', [start, end]);
            }
          }, {
            text: '最近一个月',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
              picker.$emit('pick', [start, end]);
            }
          },
        ]
      },

      invitationStateList: [{
        value: '',
        label: '全部'
      }, {
        value: '0',
        label: '无'
      }, {
        value: '1',
        label: '有'
      }],
    }
  },
  created() {
    this.searchList()
    this.searchGroupList()
  },
  mounted() {

  },
  methods: {
    //查询分销员分组
    searchGroupList() {
      initChannelGroupList({
        shopId: this.shop.shopId
      }).then(res => {
        if (res.code == 200) {
          this.groupList = res.data
        } else {
          this.$notify.error({
            title: '错误',
            message: res.message
          })
        }
      }).catch(err => {
        console.log(err)
      })
    },
    //查询数据列表
    searchList() {
      this.loading = true
      channelUserList({
        shopId: this.shop.shopId,
        pageNum: this.pageNum,
        pageSize: this.page_size,
        channelUsernickName: this.list.channelUsernickName,
        channelUserPhone: this.list.channelUserPhone,
        invitationState: this.list.invitationState,
        inviterPhone: this.list.inviterPhone,
        inviternickName: this.list.inviternickName,
        groupId: this.list.groupId,
        startCreateTime: this.list.registerTime == null ? '' : this.list.registerTime[0],
        endCreateTime: this.list.registerTime == null ? '' : this.list.registerTime[1],
      }).then(res => {
        if (res.code == 200) {
          this.tableList = res.data.list
          this.total = res.data.total
          this.loading = false
        } else {
          this.$notify.error({
            title: '错误',
            message: res.message
          });
        }
      }).catch(err => {
        console.log(err)
      })
    },

    //批量修改分组
    handleSelectionChange(val) {
      this.multipleSelection = val
    },
    //批量修改分组
    changeGroup() {
      if (this.multipleSelection.length == 0) {
        this.$message({
          message: '请选择要操作的分销员',
          type: 'warning',
          duration: 1000
        });
        return;
      }
      this.dialogFormVisibleGroup = true
      let obj = this.groupList.find(item =>{
        return item.isDefault == 1
      })
      this.group.groupId = obj.id
    },


    //批量修改分组
    setDistriGroup() {
      //验证分组人数是否超过
      groupCapacity({
        shopId: this.shop.shopId,
        groupId: this.group.groupId,
        count: this.multipleSelection.length
      }).then(res => {
        if (res.data == true) {
          let arr = this.multipleSelection.map(item => item.cUserId)
          changeChannelUserGroup({
            shopId: this.shop.shopId,
            groupId: this.group.groupId,
            ids: arr
          }).then(res => {
            if (res.data == true) {
              this.$notify({
                title: '成功',
                type: 'success',
                message: res.message
              })
              this.dialogFormVisibleGroup = false
              this.searchList()
            } else {
              this.$notify.error({
                title: '失败',
                message: res.message
              })
            }
          })
        } else {
          this.$notify.error({
            title: '错误',
            message: res.message
          })
        }
      })
    },

    //查看绑定用户
    showDetails(row) {
      this.$router.push({
        path: '/distributionManagement/distributor/juniorDistributor',
        query: {
          id: row.cUserId,
          channelUsernickName: row.channelUsernickName,
          channelGroupName: row.channelGroupName,
          channelUserPhone: row.channelUserPhone
        }
      })
    },

    //打开冻结和解冻dialog
    auditTrue(index, row) {
      if (index == 0) {
        this.dialogFormVisible = true
      }
      if (index == 1) {
        this.dialogFormVisibleOne = true
      }
      this.id = row.id
    },


    // 分销员更改状态
    audit(index) {
      updateChannelUserStatus({
        shopId: this.shop.shopId,
        id: this.id,
        state: index   //  0：正常 1.锁定
      }).then(res => {
        if (res.code == 200) {
          this.$notify({
            title: '成功',
            type: 'success',
            message: '操作成功',
            duration: 3000
          })
          this.dialogFormVisible = false
          this.dialogFormVisibleOne = false
          this.searchList()
        } else {
          this.$notify.error({
            message: res.message,
            title: '失败'
          })
        }
      }).catch(err => {
        console.log(err)
      })
    },

  //导出excel
  exportExcel() {
    let data = {
      shopId: this.shop.shopId,
        pageNum: this.pageNum,
        pageSize: this.page_size,
        channelUsernickName: this.list.channelUsernickName,
        channelUserPhone: this.list.channelUserPhone,
        invitationState: this.list.invitationState,
        inviterPhone: this.list.inviterPhone,
        inviternickName: this.list.inviternickName,
        groupId: this.list.groupId,
        startCreateTime: this.list.registerTime == null ? '' : this.list.registerTime[0],
        endCreateTime: this.list.registerTime == null ? '' : this.list.registerTime[1],
    }
    let key = objectUrl(data)
    window.open(`/sys/business/shopChannel/exportChannelUserInfo?${key}`)
  },

  handleClose() {
    this.dialogFormVisibleTwo = false
    this.form.name = ''
    this.form.orderID = undefined
    this.orderNo = ''
    this.form.month = ''
    this.form.buyTime = ''
  },
  //重置
  reset() {
    this.list = {
      channelUsernickName: '',
      channelUserPhone: '',
      invitationState: '',
      inviternickName: '',
      inviterPhone: '',
      groupId: '',
      registerTime: '',
    }
    this.searchList()
  },
  //修改每页页数
  handleSizeChange(page) {
    this.page_size = page
    this.searchList()
  },
  //点击跳转页码
  pageChange(page) {
    this.pageNum = page
    this.searchList()
  },
},
computed: {
  startTime() {
    return this.status;
  },
},
watch: {
  // startTime(newValue, oldValue) {
  //   this.searchList()
  //   return newValue;
  // },
}
}
</script>

<style lang="scss" scoped>
.btn-add-export {
  float: right;
  margin-top: -8px;
}
.operate-container {
  margin-top: 10px;
}
.store-red {
  margin-left: 70px;
  color: red;
  span {
    display: block;
    margin-bottom: 10px;
  }
}
.table-container {
  margin-top: 10px;
}
</style>
